#PLP-SurCampus, #PLP-HorsCampus{
    font-family: "Roboto Thin", sans-serif;
    cursor: pointer;
}

#PLP-surCampusOuNon{
    display: grid;
    grid-template-areas:
            "a a"
            "b c";
    justify-content: center;
    justify-items: center;
    align-items: center;
}

#PLP-surCampus-question{grid-area: a;}
#PLP-SurCampus{grid-area: b;}
#PLP-horsCampus{grid-area: c;}

#PLP-prez-contact{
    display: grid;
    grid-template-columns: 40vw 40vw;
    justify-content: center;
}
#PLP-president-h1, #PLP-contactSpe-h1 {text-align: right;}
#PLP-description{display: grid; justify-items: center}
#PLP-description-h1{justify-self: left; margin-left: 15vw}
#PLP-description-textarea{text-align: left; resize: none; padding: 10px; width: 75vw}
#PLP-description-textarea::placeholder{font-weight: lighter; font-style: italic}

@media only screen and (min-width: 768px) {
    #PLP-SurCampus, #PLP-HorsCampus{font-size: 20px; padding: 10px;}
    #PLP-surCampusOuNon{grid-gap: 10px; margin: 30px}
    .PLP-retours-asso-manif{grid-area: b; justify-self: left; margin-left: 32px}
    #PLP-Dates{margin-bottom: 30px}
    #PLP-prez-contact{margin-top: 30px; margin-bottom: 30px; margin-right: 15vw}
    #PLP-description{margin-bottom: 30px}
}

@media only screen and (max-width: 767px) {
    #PLP-SurCampus, #PLP-HorsCampus{font-size: 20px;}
}