#ManifestationAssociative-Container{
    background-color: white;
    width: 100vw;
}

.PLP-questions{
    font-family: "Poiret One", serif;
    color: #13435b;
    font-weight: normal;
}

.PLP-retours {
    border-style: solid;
    border-color: #13435b;
    border-width: 0.05em;
    margin: 2px;
    font-family: "Roboto Thin", sans-serif;
    width: 80%;
    text-align: center;
}

.PLP-Dates-containers,.PLP-asso-manif {display: grid; justify-content: center}

@media only screen and (min-width: 768px) {
    #ManifestationAssociative-Container{padding-top: 85px;}
    .PLP-questions{font-size: 25px; margin: 5px}
    .PLP-retours{font-size: 25px}
    .PLP-asso-manif{
        grid-template-areas: "a b b";
        grid-template-columns: 500px 200px 200px;
        justify-items: right;
    }
    .PLP-Dates-containers{
        grid-template-columns: 500px 200px 100px;
        justify-items: right;
    }
}

@media only screen and (max-width: 767px) {
    #ManifestationAssociative-Container{padding-top: 85px;}
    .PLP-questions{font-size: 20px}
}